"use strict";

var minimatch = require("minimatch");

function handleResponseCode(response) {
  var status = parseInt(response.status, 10);
  var err;

  if (status >= 400) {
    err = new Error("Invalid response: " + status + " " + response.statusText);
    err.status = status;
    throw err;
  }

  return response;
}

function processGlobFilter(files, glob) {
  return files.filter(function (file) {
    return minimatch(file.filename, glob, {
      matchBase: true
    });
  });
}

function processResponsePayload(response, data) {
  var isDetailed = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : false;
  return isDetailed ? {
    data: data,
    headers: response.headers || {}
  } : data;
}

module.exports = {
  handleResponseCode: handleResponseCode,
  processGlobFilter: processGlobFilter,
  processResponsePayload: processResponsePayload
};